
.fantastic {
	background-color: var(--blue);
	box-shadow: 0 1px 1px rgba(69,89,117, .9) inset;
}
.fantastic-container {
	max-width: 844px;
	padding-left: 2px;
	padding-right: 2px;
	margin-left: auto;
	margin-right: auto;
	
}
.fantastic-holder {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	padding-top: 60px;
	padding-bottom: 60px;
	position: relative;
}
.fantastic-box {
	width: 394px;
	color: var(--white);
}
.fantastic-title { 
	text-transform: uppercase;
	font-size: 15px;
}
.fantastic-description {
	padding: 34px 27px 67px 2px;
}
.fantastic-img {
	margin-left: 318px;
	margin-top: -37px;
	position: absolute;
	z-index: 2;
}
.wicked {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 76vh;
}
.wicked::before {
	content: '';
	width: 100%;
	height: 76%;
	position: absolute;
	opacity: 0.8;
	background-color: var(--red);
	z-index: 1;
	background-repeat: no-repeat;
}
.wicked-container {
	max-width: 500px;
	padding-left: 2px;
	padding-right: 2px;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	justify-content: center;
}
.wicked-holder {
	text-align: center;
	padding: 156px 0 0 0;
	color: var(--white);
	z-index: 3;
	position: absolute;
	padding-right: 77px;
	padding-left: 22px;
}
.wicked-button {
	width: 280px;
	height: 60px;
	border-radius: 30px;
	background-color: var(--white);
	text-align: center;
	text-decoration: none;
	font-size: 18px;
	color: var(--red);
	font-weight: 600;
	display: inline-block;
	vertical-align: middle;
}
.wicked-button-befor {
	height: 100%;
	display: inline-block;
	vertical-align: middle;
}
.wicked-title {
	font-size: 35px;
	font-weight: 300;
	padding-bottom: 19px;
}
.wicked-description {
	line-height: 2;
	font-size: 15px;
	padding-bottom: 32px;
}