.background-header-welcome {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 141vh;

}
.header-line {
	margin: 1px auto; 
	box-shadow: 0 1px 0.1px -1px rgb(245, 245, 245);
}
.container {
	max-width: 1600px;
	padding-left: 207px;
	padding-right: 5px;
	margin-left: auto;
	margin-right: auto;
}
.header {
	height: 97px;
	display: flex;
	justify-content: space-between;
	flex-wrap: nowrap;
}
.logo {
	font-style: italic;
	padding-top: 35px;
	color: #fff;
	font-size: 8px;
	width: 35%;
}
.logo span {
	font-family: "Time New Roman";
}
.pex {
	color: var(--red);
	font-weight: 800;
	font-size: 25px;
	padding-right: 0.36949%;
	font-family: "Time New Roman";
}
.nav {
	display: flex;
	flex-direction: row;
	height: 97px;
}
.nav_link {
	align-content: space-around;
}
.nav a {
	text-decoration: none;
	color: var(--white);
	padding-right: 56px;
	position: relative;
	padding-top: 40px;
}

.nav_link:hover::before {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	width: 50%;
	border-bottom: 2px solid #fff;
	content:'';
}
.nav_link_second:hover::before {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	width: 70%;
	border-bottom: 2px solid #fff;
	content:'';
}
.search {
	width: 222px;
	height: 44px;
	position: relative;
	padding-top: 31px;
	color: var(--grey);
}
.search input {
	border: none;
	color: var(--grey);
}
.search .input {
	width: 100%;
	padding: 11px 37px 11px 15px;
	box-sizing: border-box;
	border-radius: 20px;
	box-shadow: 
	inset 0 0 1px rgba(0,0,0,0.1), 
	inset 0 0 1px rgba(0,0,0,0.1);
	font-size: 13px;

}
.search .submit {
	position: absolute;
	top: 0;
 	right: 0;
	width: 50px;
	height: 221%;
	cursor: pointer;
	background: url(https://lh4.googleusercontent.com/-b-5aBxcxarY/UAfFW9lVyjI/AAAAAAAABUg/gQtEXuPuIds/s13/go.png)
	50% no-repeat;
	opacity: 0.4;
}
.search .submit:hover {
  	opacity: 0.6;
}
input[type="search"] {
  -webkit-appearance: none;
}
.container-welcome {
	max-width: 1600px;
	padding-left: 40px;
}
.welcome {
	padding-top: 274px;
	width: 45%;
	text-align: right;
}
.welcome-title {
	color: var(--white);
	font-size: 50px;
	font-weight: 400;
	padding-bottom: 50px;
}
.welcome-button {
	width: 280px;
	height: 60px;
	border-radius: 30px;
	background-color: var(--purple_2);
	text-align: center;
	text-decoration: none;
	font-size: 18px;
	color: var(--white);
	font-weight: 600;
	display: inline-block;
	vertical-align: middle;
	box-shadow: 
	0 2px 2px rgba(255,255,255, .1) inset, 
	0 2px 0 -2px rgba(0,0,0, .1);
}
.welcome-button:hover {
	box-shadow: 
	0 2px 2px rgba(255,255,255, .2) inset, 
	0 3px 0 -2px rgba(0,0,0, .2);
}
.welcome-button-befor {
	height: 100%;
	display: inline-block;
	vertical-align: middle;
}
.welcome p {
	padding-top: 18px;
	color: var(--white);
	font-size: 12px;
	padding-right: 27px;
}