.pen {
	max-width: 1600px;
	padding-right: 5px;
	margin-left: auto;
	margin-right: auto;
}
.pen-container {
	display: flex;
	justify-content: space-between;
}
.pen-img-holder {
	font-size: 0;
}
.pen-content-holder {
	padding: 95px 161px  10px 104px;
	color: var(--blue);
}
.pen-title {
	font-size: 15px;
	font-weight: 800;
	text-transform: uppercase;
	line-height: 2;
	padding-bottom: 10px;
}
.pen-discription {
	font-size: 19px;
	padding-bottom: 29px;
}
.pen-subdiscription {
	font-size: 15px;
	line-height: 1.5;
}
.experience {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 80vh;
}
.experience::before {
	content: '';
	margin-top: 126px;
	width: 100%;
	height: 63%;
	position: absolute;
	opacity: 0.8;
	background-color: var(--purple);
	z-index: 1;
	background-repeat: no-repeat;
}
.experience-container {
	max-width: 1600px;
	padding-right: 5px;
	margin-left: auto;
	margin-right: auto;
	position: absolute;
	z-index: 2;
}
